import { Link, Route, Routes } from "react-router-dom";
import FilePreview from "../../components/file-preview";
import Icon from "../../components/icon";
import PackageBoards from "./boards";
import PackageCompatibility from "./compatibility";
import PackageExamples from "./examples";
import PackageInsights from "./insights";
import PackageInstallation from "./installation";
import PackageSidebar from "./sidebar";
import PackageVersions from "./versions";
import PropTypes from "prop-types";
import { capitalize } from "../../helpers";
import { makePackageUrl } from "./page";

function PackageTabs(props) {
  const tabs = [
    {
      name: "readme",
      icon: "book",
      active: props.leftoverPath === "readme",
    },
    {
      name: "installation",
      icon: "download",
      active: props.leftoverPath === "installation",
    },
    {
      name: "compatibility",
      icon: "check-circle",
      active: props.leftoverPath === "compatibility",
      disabled: props.data.type === "platform",
    },
    {
      name: "boards",
      icon: "cpu",
      counter: props.data.boards_count || 0,
      active: props.leftoverPath === "boards",
      disabled: props.data.type !== "platform",
      noSidebar: true,
    },
    {
      name: "examples",
      icon: "code",
      counter: props.data.examples_count || 0,
      active: props.leftoverPath.startsWith("examples"),
      disabled: props.data.type === "tool",
      noSidebar: true,
    },
    {
      name: "insights",
      icon: "graph",
      active: props.leftoverPath === "insights",
      disabled: true,
    },
  ];
  const noSidebar = tabs.find((item) => item.active && item.noSidebar);
  const routes = (
    <Routes>
      <Route
        path="/installation"
        element={
          <PackageInstallation
            data={props.data}
            makeSelfUrl={props.makeSelfUrl}
          />
        }
      />
      <Route path="/boards" element={<PackageBoards data={props.data} />} />
      <Route
        path="/compatibility"
        element={<PackageCompatibility data={props.data} />}
      />
      <Route
        path="/examples/*"
        element={
          <PackageExamples data={props.data} makeSelfUrl={props.makeSelfUrl} />
        }
      />
      <Route
        path="/insights/*"
        element={
          <PackageInsights data={props.data} makeSelfUrl={props.makeSelfUrl} />
        }
      />
      <Route
        path="/versions"
        element={
          <PackageVersions data={props.data} makeSelfUrl={props.makeSelfUrl} />
        }
      />
      <Route
        path="*"
        element={
          props.data.readme_url ? (
            <FilePreview url={props.data.readme_url} />
          ) : (
            props.data.description
          )
        }
      />
    </Routes>
  );
  return (
    <>
      <nav>
        <ul className="package-tabs nav nav-pills nav-fill flex-column flex-lg-row">
          {tabs
            .filter((tab) => !tab.disabled)
            .map((tab) => (
              <li key={tab.name} className="nav-item">
                <Link
                  className={`nav-link ${tab.active ? "active" : ""}`}
                  aria-controls={tab.name}
                  aria-selected={tab.active ? "true" : "false"}
                  to={makePackageUrl({
                    type: props.data.type,
                    ownername: props.data.owner.username,
                    name: props.data.name,
                    extraPath: tab.name !== "readme" ? tab.name : undefined,
                    version: !props.data.version.is_latest
                      ? props.data.version.name
                      : undefined,
                  })}
                >
                  <Icon name={tab.icon} />
                  {capitalize(tab.name)}
                  {tab.counter ? (
                    <span className="badge rounded-pill bg-gray-200 text-dark ms-2">
                      {tab.counter}
                    </span>
                  ) : null}
                </Link>
              </li>
            ))}
        </ul>
      </nav>
      {noSidebar ? (
        <div className="py-3">{routes}</div>
      ) : (
        <div className="row py-3">
          <div className="col-12 col-lg-8 col-xxl-9">{routes}</div>
          <div className="col-12 col-lg-4 col-xxl-3 mt-4 mt-lg-0">
            <PackageSidebar data={props.data} makeSelfUrl={props.makeSelfUrl} />
          </div>
        </div>
      )}
    </>
  );
}

PackageTabs.propTypes = {
  data: PropTypes.shape({
    type: PropTypes.string.isRequired,
    owner: PropTypes.shape({
      username: PropTypes.string.isRequired,
    }).isRequired,
    name: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    version: PropTypes.shape({
      name: PropTypes.string.isRequired,
      released_at: PropTypes.string.isRequired,
      is_latest: PropTypes.bool.isRequired,
    }).isRequired,
    keywords: PropTypes.arrayOf(PropTypes.string),
    readme_url: PropTypes.string,
    examples_count: PropTypes.number,
    boards_count: PropTypes.number,
  }).isRequired,
  leftoverPath: PropTypes.string,
  makeSelfUrl: PropTypes.func.isRequired,
};

PackageTabs.defaultProps = {
  leftoverPath: "readme",
};

export default PackageTabs;
