import React, { useEffect, useState } from "react";
import { useFilters, usePagination, useSortBy, useTable } from "react-table";
import { CopyToClipboard } from "react-copy-to-clipboard";
import Humanize from "../../components/humanize";
import Icon from "../../components/icon";
import { Link } from "react-router-dom";
import Pagination from "../../components/pagination";
import PropTypes from "prop-types";
import Spinner from "../../components/spinner";
import { enableBStooltips } from "../../helpers";
import { makeSearchUrl } from "./../search/page";
import { usePackageData } from "../../data";
import { useSearchParams } from "react-router-dom";

export default function PackageBoards(props) {
  const [searchParams] = useSearchParams();
  const customVersion = searchParams.get("version");
  const customPage = searchParams.get("p");
  const { data, isLoading, isError } = usePackageData({
    type: props.data.type,
    ownername: props.data.owner.username,
    name: props.data.name,
    version: customVersion,
    boards: true,
  });
  const makeBoardDocsUrl = (board) => {
    return `https://docs.platformio.org/en/latest/boards/${props.data.name}/${board}.html`;
  };
  const defaultColumn = React.useMemo(
    () => ({
      Filter: TextColumnFilter,
      disableFilters: true,
    }),
    []
  );
  const columns = React.useMemo(
    () => [
      {
        Header: "Name",
        accessor: "name",
        disableFilters: false,
        // eslint-disable-next-line react/prop-types
        Cell: ({ row, value }) => (
          <NameColumnCell
            // eslint-disable-next-line react/prop-types
            boardId={row.id}
            boardName={value}
            // eslint-disable-next-line react/prop-types
            docsUrl={makeBoardDocsUrl(row.id)}
          />
        ),
      },
      {
        Header: "MCU",
        accessor: "mcu",
        disableFilters: false,
      },
      {
        Header: "Frequency",
        accessor: "fcpu",
        // eslint-disable-next-line react/prop-types
        Cell: ({ value }) => (
          <span className="text-nowrap">{parseInt(value) / 1000000} MHz</span>
        ),
      },
      {
        Header: "RAM",
        accessor: "ram",
        // eslint-disable-next-line react/prop-types
        Cell: ({ value }) => (
          <span className="text-nowrap">
            <Humanize type="size" value={value} />
          </span>
        ),
      },
      {
        Header: "Flash",
        accessor: "rom",
        // eslint-disable-next-line react/prop-types
        Cell: ({ value }) => (
          <span className="text-nowrap">
            <Humanize type="size" value={value} />
          </span>
        ),
      },
      {
        Header: "Frameworks",
        accessor: "frameworks",
        // eslint-disable-next-line react/prop-types
        Cell: ({ value }) =>
          value.map((item) => (
            <span
              key={item}
              className="badge rounded-pill text-primary bg-info me-1"
            >
              <Link
                key={item}
                to={makeSearchUrl({ query: item, type: "tool" })}
              >
                {item}
              </Link>
            </span>
          )),
        disableFilters: false,
        Filter: SelectColumnFilter,
        filter: "includes",
      },
      {
        Header: "Debug",
        accessor: "debug",
        // eslint-disable-next-line react/prop-types
        Cell: ({ row, value }) => (
          <DebugColumnCell
            tools={(value || {}).tools}
            // eslint-disable-next-line react/prop-types
            docsUrl={makeBoardDocsUrl(row.id)}
          />
        ),
        disableFilters: false,
        Filter: SelectColumnFilter,
        filter: DebugColumnFilterFn,
      },
    ],
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  if (isLoading || isError) return <Spinner />;
  if (!data || !data.length) {
    return (
      <div className="card text-center">
        <div className="card-body">
          <h5 className="card-title">No boards</h5>
          <p className="card-text">
            The <b>{props.data.name}</b> platform does not provide any boards.
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className="table-responsive boards">
      <BoardTable
        columns={columns}
        defaultColumn={defaultColumn}
        data={data}
        customPage={customPage}
      />
    </div>
  );
}

PackageBoards.propTypes = {
  data: PropTypes.shape({
    type: PropTypes.string.isRequired,
    owner: PropTypes.shape({
      username: PropTypes.string.isRequired,
    }).isRequired,
    name: PropTypes.string.isRequired,
  }).isRequired,
};

function BoardTable({ columns, data, defaultColumn, customPage }) {
  useEffect(() => {
    enableBStooltips();
  });
  const {
    state: { pageIndex, pageSize },
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page,
    setPageSize,
    gotoPage,
    pageCount,
  } = useTable(
    {
      columns,
      data: data || [],
      defaultColumn,
      initialState: {
        pageIndex: customPage ? parseInt(customPage) - 1 : 0,
        pageSize: 15,
        sortBy: [{ id: "name" }],
      },
      getRowId: (row) => row.id,
    },
    useFilters,
    useSortBy,
    usePagination
  );
  const [activeFilters, setActiveFilters] = useState([]);
  const toggleFilter = (id) =>
    setActiveFilters(
      activeFilters.includes(id)
        ? activeFilters.filter((item) => item !== id)
        : [...activeFilters, ...[id]]
    );
  const [searchParams] = useSearchParams();
  const makePaginationUrl = (page) => {
    const qs = new URLSearchParams(searchParams);
    qs.set("p", page);
    return "?" + qs.toString();
  };
  return (
    <>
      <table className="table table-hover" {...getTableProps()}>
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th scope="col" {...column.getHeaderProps()}>
                  <div className="hstack">
                    <div {...column.getSortByToggleProps()}>
                      {column.render("Header")}
                    </div>
                    <div className="text-primary">
                      {column.isSorted ? (
                        column.isSortedDesc ? (
                          <Icon name="triangle-down" size="18" />
                        ) : (
                          <Icon name="triangle-up" size="18" />
                        )
                      ) : null}
                    </div>
                    {column.canFilter ? (
                      <div
                        role="button"
                        tabIndex="0"
                        className={`ps-2 ${
                          activeFilters.includes(column.id)
                            ? "text-primary"
                            : "text-secondary"
                        }`}
                        onClick={() => toggleFilter(column.id)}
                      >
                        <Icon
                          name={
                            ["name", "mcu"].includes(column.id)
                              ? "search"
                              : "filter"
                          }
                        />
                      </div>
                    ) : null}
                  </div>
                  {column.canFilter && activeFilters.includes(column.id) ? (
                    <div className="mt-1">{column.render("Filter")}</div>
                  ) : null}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {page.map((row) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map((cell) => {
                  return (
                    <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </table>
      {pageCount > 1 ? (
        <div className="d-flex justify-content-center flex-wrap pt-2">
          <div>
            <Pagination
              total={pageCount * pageSize - 1}
              currentPage={pageIndex + 1}
              resultsPerPage={pageSize}
              makePageUrl={makePaginationUrl}
              onPageClick={(page) => gotoPage(page - 1)}
            />
          </div>
          <div>
            <select
              className="form-select ms-2"
              value={pageSize}
              onChange={(e) => {
                setPageSize(Number(e.target.value));
              }}
            >
              {[15, 25, 50, 100, 1000].map((pageSize) => (
                <option key={pageSize} value={pageSize}>
                  Show {pageSize}
                </option>
              ))}
            </select>
          </div>
        </div>
      ) : null}
    </>
  );
}

BoardTable.propTypes = {
  columns: PropTypes.array.isRequired,
  defaultColumn: PropTypes.object.isRequired,
  data: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired,
      mcu: PropTypes.string.isRequired,
      fcpu: PropTypes.number.isRequired,
      ram: PropTypes.number.isRequired,
      rom: PropTypes.number.isRequired,
      frameworks: PropTypes.arrayOf(PropTypes.string.isRequired).isRequired,
      debug: PropTypes.object,
    })
  ).isRequired,
  customPage: PropTypes.number,
};

/** TextColumnFilter */

function TextColumnFilter({ column: { Header, filterValue, setFilter } }) {
  return (
    <input
      className="form-control form-control-sm position-relative"
      value={filterValue || ""}
      onChange={(e) => {
        setFilter(e.target.value || undefined);
      }}
      placeholder={`Filter by ${Header}...`}
    />
  );
}

TextColumnFilter.propTypes = {
  column: PropTypes.shape({
    Header: PropTypes.string.isRequired,
    setFilter: PropTypes.func.isRequired,
    filterValue: PropTypes.string,
  }).isRequired,
};

/** SelectColumnFilter */

function SelectColumnFilter({
  column: { id, filterValue, setFilter, preFilteredRows },
}) {
  const debugOptions = React.useMemo(() => {
    return ["On-board", "External", "Not available"];
  }, []);
  const genericOptions = React.useMemo(() => {
    if (id === "debug") return [];
    const options = new Set();
    preFilteredRows.forEach((row) =>
      (row.values[id] || []).forEach((item) => options.add(item))
    );
    return [...options.values()];
  }, [id, preFilteredRows]);
  return (
    <select
      className="form-control form-control-sm"
      value={filterValue}
      onChange={(e) => {
        setFilter(e.target.value || undefined);
      }}
    >
      <option value="">Any</option>
      {(id === "debug" ? debugOptions : genericOptions).map((option, i) => (
        <option key={i} value={option}>
          {option}
        </option>
      ))}
    </select>
  );
}

SelectColumnFilter.propTypes = {
  column: PropTypes.shape({
    id: PropTypes.string.isRequired,
    setFilter: PropTypes.func.isRequired,
    preFilteredRows: PropTypes.array.isRequired,
    filterValue: PropTypes.string,
  }).isRequired,
};

/** CopyBoardId */

function CopyBoardId(props) {
  const [copied, setCopied] = useState(false);
  const onCopy = () => {
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  return (
    <CopyToClipboard text={props.id} onCopy={onCopy}>
      <span
        role="button"
        data-bs-toggle="tooltip"
        title={`Click to copy board ID: ${props.id}`}
      >
        {copied ? (
          <small className="text-success">Copied!</small>
        ) : (
          <Icon name="copy" size="16" />
        )}
      </span>
    </CopyToClipboard>
  );
}

CopyBoardId.propTypes = {
  id: PropTypes.string.isRequired,
};

/** NameColumnCell */

function NameColumnCell({ docsUrl, boardId, boardName }) {
  return (
    <div className="hstack">
      <div>
        <a href={docsUrl} target="_blank" rel="noopener noreferrer">
          {boardName}
        </a>
      </div>
      <div className="ps-2 board-id">
        <CopyBoardId id={boardId} />
      </div>
    </div>
  );
}

NameColumnCell.propTypes = {
  docsUrl: PropTypes.string.isRequired,
  boardId: PropTypes.string.isRequired,
  boardName: PropTypes.string.isRequired,
};

/** DebugColumnCell */

function DebugColumnCell(props) {
  if (!props.tools) {
    return (
      <span
        className="text-secondary"
        data-bs-toggle="tooltip"
        title="Not available"
      >
        <Icon name="x" size="20" />
      </span>
    );
  }
  const onboard = [];
  const external = [];
  for (const [tool, options] of Object.entries(props.tools)) {
    if (options.onboard) {
      onboard.push(tool);
    } else {
      external.push(tool);
    }
  }
  if (!onboard.length && !external.length) return null;
  const title = [];
  if (onboard.length) {
    title.push(`ON-BOARD: ${onboard.join(", ")}`);
  }
  if (external.length) {
    title.push(`EXTERNAL: ${external.join(", ")}`);
  }
  return (
    <a
      href={`${props.docsUrl}#debugging`}
      target="_blank"
      rel="noopener noreferrer"
      data-bs-toggle="tooltip"
      title={title.join("\n")}
    >
      {onboard.length ? "On-board" : "External"}
    </a>
  );
}

DebugColumnCell.propTypes = {
  docsUrl: PropTypes.string.isRequired,
  tools: PropTypes.object,
};

function DebugColumnFilterFn(rows, columnIds, filterValue) {
  if (!filterValue) return rows;
  return rows.filter((row) => {
    if (!filterValue) return true;
    const tools = Object.keys((row.original.debug || {}).tools || {});
    switch (filterValue) {
      case "Not available":
        return !tools.length;
      case "On-board":
        return tools.some((tool) => row.original.debug.tools[tool].onboard);
      case "External":
        return tools.some((tool) => !row.original.debug.tools[tool].onboard);
      default:
        return false;
    }
  });
}
