import { Tooltip } from "bootstrap";

export function parseRepositoryUrl(url) {
  if (!url) return null;
  let parsedUrl = null;
  try {
    parsedUrl = new URL(url);
  } catch (err) {
    return null;
  }
  if (!["github.com", "gitlab.com"].includes(parsedUrl.hostname)) {
    return null;
  }
  const pathParts = parsedUrl.pathname.split("/");
  if (pathParts.length < 3) {
    return null;
  }
  let [, username, reponame] = pathParts;
  if (reponame.endsWith(".git")) {
    reponame = reponame.slice(0, -4);
  }
  return {
    provider: parsedUrl.hostname.split(".")[0],
    username,
    reponame,
  };
}

export function capitalize(string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}

export function valueCmp(a, b) {
  if (a > b) return 1;
  if (a < b) return -1;
  return 0;
}

export function enableBStooltips() {
  const tooltipTriggerList = [].slice.call(
    document.querySelectorAll('[data-bs-toggle="tooltip"]')
  );
  tooltipTriggerList.forEach((item) => Tooltip.getOrCreateInstance(item));
}
