import Icon from "./icon";
import { Link } from "react-router-dom";
import Paginator from "paginator";
import PropTypes from "prop-types";

function Pagination(props) {
  if (props.total <= props.resultsPerPage) return "";
  const pagination = new Paginator(
    props.resultsPerPage,
    props.pageLength
  ).build(props.total, props.currentPage);
  const pages = [];
  if (pagination.first_page >= 4) {
    pages.push(...[1, 2, null]);
  }
  let p = pagination.first_page;
  while (p <= pagination.last_page) {
    pages.push(p);
    p++;
  }
  if (pagination.last_page + 2 < pagination.total_pages) {
    pages.push(...[null, pagination.total_pages - 1, pagination.total_pages]);
  }

  return (
    <nav aria-label="Page navigation">
      <ul className="pagination justify-content-center flex-wrap">
        <li
          className={`page-item ${
            !pagination.has_previous_page ? "disabled" : ""
          }`}
        >
          <Link
            className="page-link"
            to={props.makePageUrl(pagination.previous_page)}
            onClick={() =>
              props.onPageClick
                ? props.onPageClick(pagination.previous_page)
                : null
            }
          >
            <Icon name="chevron-left" size="18" /> Previous
          </Link>
        </li>
        {pages.map((page, index) => (
          <li
            key={index}
            className={
              page === pagination.current_page
                ? "page-item active"
                : page
                ? "page-item"
                : "pt-1"
            }
          >
            {page ? (
              <Link
                className="page-link"
                to={props.makePageUrl(page)}
                onClick={() =>
                  props.onPageClick ? props.onPageClick(page) : null
                }
              >
                {page}
              </Link>
            ) : (
              "..."
            )}
          </li>
        ))}
        <li
          className={`page-item ${!pagination.has_next_page ? "disabled" : ""}`}
        >
          <Link
            className="page-link"
            to={props.makePageUrl(pagination.next_page)}
            onClick={() =>
              props.onPageClick ? props.onPageClick(pagination.next_page) : null
            }
          >
            Next <Icon name="chevron-right" size="18" />
          </Link>
        </li>
      </ul>
    </nav>
  );
}

Pagination.defaultProps = {
  pageLength: 5,
};

Pagination.propTypes = {
  total: PropTypes.number.isRequired,
  currentPage: PropTypes.number.isRequired,
  resultsPerPage: PropTypes.number.isRequired,
  makePageUrl: PropTypes.func.isRequired,
  pageLength: PropTypes.number,
  onPageClick: PropTypes.func,
};

export default Pagination;
