import Icon from "./icon";
import { REGISTRY_FRONTEND_GH_REPONAME } from "../constants";
import React from "react";

export default class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { error: null, errorInfo: null };
  }

  componentDidCatch(error, errorInfo) {
    // Catch errors in any components below and re-render with error message
    this.setState({
      error: error,
      errorInfo: errorInfo,
    });
    // You can also log error messages to an error reporting service here
  }

  render() {
    if (!this.state.errorInfo) {
      // eslint-disable-next-line react/prop-types
      return this.props.children;
    }
    const qs = new URLSearchParams();
    qs.set(
      "title",
      `[Error] ${this.state.error ? this.state.error.toString() : ""}`
    );
    qs.set(
      "body",
      `
# Page
${window.location.href}

# Stack
\`\`\`
${this.state.errorInfo.componentStack.toString().trim()}
\`\`\``.trim()
    );
    return (
      <div className="container py-5 w-75">
        <div className="alert alert-danger" role="alert">
          <h4 className="alert-heading">
            <Icon name="alert" size="32" />
            <span className="ps-2">Something went wrong</span>.
          </h4>
          <p>{this.state.error ? this.state.error.toString() : null}</p>
          <a
            href={`https://github.com/platformio/${REGISTRY_FRONTEND_GH_REPONAME}/issues/new?${qs.toString()}`}
            className="btn btn-danger my-3"
            target="_blank"
            rel="noopener noreferrer"
          >
            Please report issue
          </a>
        </div>
      </div>
    );
  }
}
