import PropTypes from "prop-types";

export default function PackageInsights(props) {
  return "Coming coon";
}

PackageInsights.propTypes = {
  data: PropTypes.shape({
    name: PropTypes.string.isRequired,
    authors: PropTypes.arrayOf(
      PropTypes.shape({
        name: PropTypes.string.isRequired,
        email: PropTypes.string,
        url: PropTypes.string,
        maintainer: PropTypes.bool,
      })
    ),
  }).isRequired,
  makeSelfUrl: PropTypes.func.isRequired,
};
